import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import * as moment from 'moment';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { UtilsService } from 'src/app/shared/utils.service';
import { ModelsService } from '../models.service';
import { MatSnackBar } from '@angular/material/snack-bar';
import { FormControl, Validators } from '@angular/forms';
import { ngxCsv } from 'ngx-csv';
import { EdaService } from '../../eda/eda.service';


const Plotly = require('plotly.js-dist');
@Component({
  selector: 'app-timeseries-expansion',
  templateUrl: './timeseries-expansion.component.html',
  styleUrls: ['./timeseries-expansion.component.scss'],
})
export class TimeseriesExpansionComponent implements OnInit, OnDestroy {
  @Input() training: any;
  @Input() model: any;
  @Input() originalFileWithEdaDetails: any;
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Variables with default values */
  backGround: string = '#ffffff';
  foreGround: string = '#08509d';
  startDate: any;
  endDate: any;
  showSpinner: boolean = false;
  showTextError: boolean = false;
  text: any;
  isError: boolean = false;
  uniqueForecastDiv: string;
  uniqueEvaluateDiv: string;
  selectedTimeseriesGroup = new FormControl('', Validators.required);
  selectedTimeseriesGroupForecast = new FormControl('', Validators.required);
  frequency: any;
  losses: any = {};
  //variable for storing the graph data in csv format.
  csvData: Array<Object> = [];
  colNameOtherThanX: any = [];
  timeseriesGoupList;
  constructor(
    public _utils: UtilsService,
    private _spinner: SpinnerService,
    private _toaster: ToastrService,
    private _models: ModelsService,
    private _snackBar: MatSnackBar,
    private _eda: EdaService,
  ) { }

  ngOnInit(): void {
    if (this.training.edaId.isMultipleTimeseries) {
      this.getTimeseriesGroup();
  }
    this.startDate = new Date();
    this.startDate = new Date(Date.now() - 864e5).toISOString().slice(0, 10);
    this.endDate = new Date().toISOString().slice(0, 10);
    this.uniqueForecastDiv = `${this.model.modelMetaData.algoName}Plot`;
    this.uniqueEvaluateDiv = `${this.model.modelMetaData.algoName}Plot`;
    this.losses = this.model.modelMetaData.losses;
  }

  getTimeseriesGroup() {
    this._spinner.showSpinner();
    let reqObj = {
      modelId: this.model._id,
    }
    this._eda
      .getTimeseriesGroup(reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this.showSpinner = true;
            this.timeseriesGoupList = res[0].allowedGroupList;
            this._spinner.stopSpinner();
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this.showSpinner = false;
          this._toaster.error(err.error.message || 'Server Error', 'Model');
        }
      );
  }
  async calculateFrequency(): Promise<any> {
    if (this.training.edaId.hasOwnProperty('resampling')) {
      this.frequency = this.training.edaId.resampling.resamplingMethod;
    } else {
      this.frequency = this.originalFileWithEdaDetails.descriptiveStatistics.filter((item) => {
        return item.colName === this.training.edaId.timeseriesInfo.datetimeIndex;
      })[0];
      this.frequency = this.frequency.frequency;
    }
  }
  /**
   * Evaluates the forcast and generates the graph
   */
  async evaluateForecast(): Promise<any> {
    await this.calculateFrequency();
    let reqObj = {
      indexPath: this.training.edaId.indexPath,
      trainPipeFilePath: this.model.trainPipeFilePath,
      parentPipelineFilename: this.training.edaId.tasks[0].currentPipelineFilename,
      filename: this.training.metaInfo.fileName,
      modelPath: this.model.modelMetaData.modelPath,
      backGround: this.backGround,
      foreGround: this.foreGround,
      depVariable: this.training.depVariable,
      timeseriesIds: this.training.edaId.isMultipleTimeseries
        ? this.training.edaId.timeseriesIds
        : null,
      selectedTimeseriesGroup: this.selectedTimeseriesGroup.value,
      isMultipleTimeseries: this.training.edaId.isMultipleTimeseries,
      nSteps: this.training.nSteps,
      frequency: this.frequency !== '' ? this.frequency : 'none',
    };
    this._spinner.showSpinner();
    this._models
      .evaluateGraph(this.training._id, this.model._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this.showSpinner = true;
            let graphDiv: any = document.getElementById(this.uniqueEvaluateDiv);
            while (graphDiv.data && graphDiv.data.length > 0) {
              Plotly.deleteTraces(graphDiv, [0]);
            }
            res.graphContent.layout.xaxis.linecolor = 'black';
            res.graphContent.layout.yaxis.linecolor = 'black';
            Plotly.plot(graphDiv, res.graphContent.data, res.graphContent.layout);
            this._spinner.stopSpinner();
          }
        },
        (err: any) => {
          this._spinner.stopSpinner();
          this.showSpinner = false;
          this._toaster.error(err.error.message || 'Server Error', 'Model');
        }
      );
  }

  /**
   * Fetches the forecast graph
   */
  async getForecast(): Promise<any> {
    this._spinner.showSpinner();
    await this.calculateFrequency();
    let reqObj = {
      indexPath: this.training.edaId.indexPath,
      trainPipeFilePath: this.model.trainPipeFilePath,
      parentPipelineFilename: this.training.edaId.tasks[0].currentPipelineFilename,
      filename: this.training.metaInfo.fileName,
      preprocessedFilepath: this.training.preprocessedFilepath,
      algorithm: this.model.modelMetaData.algoName,
      startDate: moment(this.startDate).format('L'),
      endDate: moment(this.endDate).format('L'),
      modelPath: this.model.modelMetaData.modelPath,
      forecastModelPath: this.model.modelMetaData.forecastModelPath,
      backGround: this.backGround,
      foreGround: this.foreGround,
      depVariable: this.training.depVariable,
      timeseriesIds: this.training.edaId.isMultipleTimeseries
        ? this.training.edaId.timeseriesIds
        : null,
      selectedTimeseriesGroup: this.selectedTimeseriesGroupForecast.value,
      isMultipleTimeseries: this.training.edaId.isMultipleTimeseries,
      nSteps: this.training.nSteps,
      frequency: this.frequency !== '' ? this.frequency : 'none',
    };
    // API call
    this._models
      .getForecastGraph(this.training._id, this.model._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.showSpinner = true;
            let graphDiv: any = document.getElementById(this.uniqueForecastDiv);
            while (graphDiv.data && graphDiv.data.length > 0) {
              Plotly.deleteTraces(graphDiv, [0]);
            }
            if (res.graphContent.layout) {
              this.showTextError = false;
              res.graphContent.layout.template.layout.title.xanchor = 'center';
              res.graphContent.layout.template.layout.title.y = 0.9;
              res.graphContent.layout.template.layout.title.x = 0.5;
              res.graphContent.layout.template.layout.title.yanchor = 'top';
              res.graphContent.layout.template.layout.xaxis.gridcolor = 'transparent';
              res.graphContent.layout.template.layout.yaxis.gridcolor = 'transparent';
              res.graphContent.layout.template.layout.yaxis.zerolinecolor = 'transparent';
              res.graphContent.layout.xaxis.linecolor = 'black';
              res.graphContent.layout.yaxis.linecolor = 'black';
              res.graphContent.layout.xaxis.showgrid = false;
              Plotly.plot(graphDiv, res.graphContent.data, res.graphContent.layout);
              this.prepareGraphData(res.graphContent.data);
              this._spinner.stopSpinner();
            } else {
              this.showTextError = true;
              this._spinner.stopSpinner();
              this.text = res.graphContent;
            }
          }
        },
        (e: any) => {
          this._spinner.stopSpinner();
          this.showSpinner = false;
          this._toaster.error(e.error.message || 'Server Error', 'Model');
        }
      );
  }

  /**
   * function to prepare the graph data in the csv format and allow user to download it.
   * @param graphData - graph data
   */
  prepareGraphData(graphData: any) {
    var transposedData: Array<Object> = [];
    this.csvData.length = 0;
    this.colNameOtherThanX = [];
    let colNames = []
    // doing transpose of the graph data.
    graphData.forEach(function (d) {
      for (var key in d) {
        if (transposedData[key] === undefined) {
          transposedData[key] = [];
        }
        (key == 'x' || key == 'y') && transposedData[key].push(d[key]);
        (key === 'name') && colNames.push(d[key]);
      }
    });
    this.colNameOtherThanX = colNames;
    // deleting all other keys of transposedData.
    for (var key in transposedData) {
      if (key != 'x' && key != 'y') {
        delete transposedData[key];
      }
    }
    // seperating the x and y axis data.
    let xdataForCsv = [];
    let ydataForCsv = [];
    for (var key in transposedData) {
      (key == 'x') && xdataForCsv.push(transposedData[key]);
      (key == 'y') && ydataForCsv.push(transposedData[key]);
    }
    xdataForCsv = xdataForCsv.toString().split(',').sort();
    xdataForCsv = [...new Set(xdataForCsv)];
    // preparing the data in csv format.
    if (!this.training.edaId.isMultipleTimeseries) {
      // logic to convert graph data to csv if not multi timeseries and is prophet algo.
      if (this.model.modelMetaData.algoName === 'Prophet') {
        for (let i = 0; i < xdataForCsv.length; i++) {
          this.csvData[i] = {};
          this.csvData[i]['x'] = xdataForCsv[i];
          for (let k = 0; k < this.colNameOtherThanX.length; k++) {
            if (i < ydataForCsv[0][0].length) {
              if (k === 0) {
                this.csvData[i][this.colNameOtherThanX[0]] = ydataForCsv[0][0][i];
              }
            } else if (i >= ydataForCsv[0][0].length) {
              if (k !== 0) {
                this.csvData[i][this.colNameOtherThanX[k]] = ydataForCsv[0][k][(i - Number(ydataForCsv[0][0].length))];
              } else {
                this.csvData[i][this.colNameOtherThanX[k]] = '';
              }
            }
          }
        }
      } else {
        // logic to convert graphdata to csv if not multi timeseries and not prophet algo.
        for (let i = 0; i < xdataForCsv.length; i++) {
          if (i <= ydataForCsv[0][0].length) {
            this.csvData.push({ x: xdataForCsv[i], y: ydataForCsv[0][0][i], forecast: ydataForCsv[0][1][i] });
          }
        }
      }
    } else {
      // logic to convert graphdata to csv if multi timeseries.
      for (let i = 0; i < xdataForCsv.length; i++) {
        if (i < ydataForCsv[0][0].length) {
          this.csvData.push({ x: xdataForCsv[i], y: ydataForCsv[0][0][i], forecast: '' });
        } else {
          this.csvData.push({ x: xdataForCsv[i], y: '', forecast: ydataForCsv[0][1][(i - Number(ydataForCsv[0][0].length))] });
        }
      }
    }
  }

  // function to download the prepared graph data in the csv format.
  downloadForecast() {
    var options = {
      fieldSeparator: ',',
      quoteStrings: '"',
      decimalseparator: '.',
      showLabels: true,
      useBom: true,
      noDownload: false, // if true, will not allow the direct download of the csv file.
      headers: ['x', ...this.colNameOtherThanX]
    };
    this.csvData.length && new ngxCsv(this.csvData, 'forecastData', options);
  }

  /**
   * Called on dates change
   * @param sDate
   * @param eDate
   * @returns
   */
  onChangeDate(sDate: any, eDate: any): number {
    let startDate = moment(sDate);
    let endDate = moment(eDate);
    let isDateCorrect = moment(endDate).isAfter(startDate, 'day');
    if (isDateCorrect === false) {
      this.isError = true;
      this._snackBar
        .open('End date should be greater than start date', 'OK', {
          horizontalPosition: 'right',
          verticalPosition: 'bottom',
        })
        .afterDismissed()
        .subscribe((data) => {
          let date = new Date(sDate);
          // add a day
          date.setDate(date.getDate() + 1);
          this.endDate = date.toISOString().slice(0, 10);
          this.isError = false;
        });
    } else {
      this.isError = false;
    }
    return 0;
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
   