import { LocalStorageService } from './../../../shared/local-storage.service';
import {
  ChangeDetectorRef,
  Component,
  EventEmitter,
  Input,
  OnInit,
  Output,
  TemplateRef,
  ViewChild,
} from '@angular/core';
import { MatSnackBar, MatSnackBarRef } from '@angular/material/snack-bar';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { ToastrService } from 'ngx-toastr';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ColorsService } from 'src/app/shared/colors/colors.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { UtilsService } from 'src/app/shared/utils.service';
import { ModelsService } from '../models.service';
import { environment } from 'src/environments/environment';
import { HttpErrorResponse } from '@angular/common/http';
import { MatPaginator } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { EdaReportComponent } from 'src/app/shared/components/eda-report/eda-report.component';
const Plotly = require('plotly.js-dist');
export interface Card {
  title: string;
  subtitle: string;
  text: string;
}
@Component({
  selector: 'app-cluster-expansion',
  templateUrl: './cluster-expansion.component.html',
  styleUrls: ['./cluster-expansion.component.scss'],
})
export class ClusterExpansionComponent implements OnInit {
  @Input() training: any;
  @Input() model: any;
  @Input() originalFileWithEdaDetails: any;
  @Input() trainings: any;
  @Output() updateTrainingModels = new EventEmitter<any>();
  project: any;
  @ViewChild(MatPaginator) set paginator(value: MatPaginator) {
    this.dataSource.paginator = value;
  }
  dialogRef: MatDialogRef<any>;
  obs: Observable<any>;
  dataSource: MatTableDataSource<[]> = new MatTableDataSource();
  pieOptions1: any = {
    animate: {
      duration: 1000,
      enabled: true,
    },
    barColor: this._colors.byName('primary'),
    trackColor: '#f2f2f2',
    scaleColor: false,
    lineWidth: 5,
    size: 65,
    lineCap: 'round',
  };
  public inputCluster;
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();
  featuresArray: Array<any> = [];
  /** Variables with default values */
  backGround: string = '#ffffff';
  foreGround: string = '#08509d';
  showSpinner: boolean = false;
  viewScatterPlot: boolean = false;
  editMode = false;
  snackBarRef: MatSnackBarRef<any>;
  selectedFeature: Array<any> = [];
  clusteringFeatures: any = [];
  uniqueGraphDiv: string;
  heatMapDiv: string;
  featureImp: string;
  decisionTreePlot: string;
  showUpdateButton: boolean = false;
  toggleView: boolean = true;
  viewType: string = 'graph';
  decisionTreeImage: SafeResourceUrl;
  cardColor = ['#f1f8fe', '#fdf4e3', '#e7e3fd'];
  showError;
  hideHeatMap: boolean = true;
  hideSummary: boolean = false;
  settingActive: number = 1;
  featureList: any = [];
  clusterErrorType: any;
  selectedCluster: any = null;

  constructor(
    public _utils: UtilsService,
    private _spinner: SpinnerService,
    private _toaster: ToastrService,
    private _snackBar: MatSnackBar,
    private _models: ModelsService,
    private _colors: ColorsService,
    private _sanitizer: DomSanitizer,
    private _changeDetectorRef: ChangeDetectorRef,
    private _dialog: MatDialog,
    private _localStorage: LocalStorageService
  ) { }

  ngOnInit(): void {
    this.project = this._localStorage.getItem('project');
    this.hideSummary = false;
    this.hideHeatMap = true;
    this.model.showUpdateButton = false;
    this.dataSource.data = this.model.modelMetaData.analysisInfo;
    this.dataSource.paginator = this.paginator;
    this._changeDetectorRef.detectChanges();
    this.obs = this.dataSource.connect();
    this.model.modelMetaData.clusteringGraphType = 'scatter';
    this.featureImp = `${this.model.modelMetaData.algoName}`;
    this.uniqueGraphDiv = `${this.model.modelMetaData.algoName}Plot`;
    this.heatMapDiv = `${this.model.modelMetaData.algoName}PlotMap`;
    this.clusteringFeatures = [];
    this.clusterErrorType = typeof this.model.modelMetaData.analysisInfo;
    // If decomposition=='pca' or factoranalysis, first priority
    //  if (this.training.clusteringInfo.decomposition != 'none') {
    //     // generate custom values for pca
    //     for (let i = 0; i < this.training.clusteringInfo.components; i++) {
    //       this.clusteringFeatures.push('component_' + i);
    //     }
    //   } else {
    if (this.training.indepVariable.length) {
      // Filter out numeric indepVariables
      let feature = this.training.edaId.edaSummary.filter((data: any) => {
        if (
          data.dataType === 'Integer' ||
          data.dataType === 'Categorical' ||
          data.dataType === 'Decimal' ||
          data.dataType === 'Text'
        ) {
          return data;
        }
      });
      feature.map((data: any) => {
        let found = this.training.indepVariable.find((indepVar: any) => {
          return indepVar.colName === data.colName;
        });
        if (found && found !== 'undefined') {
          // tslint:disable-next-line: no-string-literal
          // return data['colName'];
          this.clusteringFeatures.push(data);
        }
      });
      this.featureList = this.clusteringFeatures;
    }
    // }
  }

  showGraphsSection(event: any): void {
    if (event.tab.textLabel === 'Heat Map') {
      this.plotHeatMap();
    } else if (event.tab.textLabel === 'Decision Tree') {
      this.plotDecisionTreePlot();
    } else if (event.tab.textLabel === 'Feature Importance') {
      this.viewType = 'graph';
      this.plotFeatureImp();
    }
  }

  featureSelectionChanged(dataItem) {
    Plotly.purge(document.getElementById(this.uniqueGraphDiv));
    if (dataItem.length && dataItem[0].dataType === 'Categorical') {
      this.clusteringFeatures = dataItem;
      this.selectedCluster = null;
    } else if (dataItem.length && dataItem[0].dataType === 'Text') {
      this.clusteringFeatures = dataItem;
    } else if (
      dataItem.length &&
      dataItem[0].dataType !== 'Categorical' &&
      dataItem[0].dataType !== 'Text'
    ) {
      this.clusteringFeatures = this.featureList.filter((item) => {
        return item.dataType !== 'Categorical' && item.dataType !== 'Text';
      });
      this.selectedCluster = null;
    } else {
      this.clusteringFeatures = this.featureList;
      this.selectedCluster = null;
    }
  }

  /**
   * plot feature imp plot
   */
  plotFeatureImp(): void {
    let graphDiv: any = document.getElementById(this.featureImp);
    let X: Array<number> = [];
    let Y: Array<string> = [];
    let graphData: Array<any> = [];
    this.featuresArray = this.sortArryOFObjects(
      this.model.modelMetaData.featureImportance,
      'importance'
    );
    this.featuresArray.length > 15 ? (this.featuresArray = this.featuresArray.slice(0, 15)) : null;
    this.featuresArray.forEach((item: any) => {
      X.push(item.importance);
      Y.push(item.colName);
    });
    // Deletes the graph data if already present
    while (graphDiv?.data?.length > 0) {
      Plotly.deleteTraces(graphDiv, [0]);
    }
    // Final graph object
    graphData = [
      {
        type: 'bar',
        x: X,
        y: Y,
        orientation: 'h',
      },
    ];
    var layout = {
      autosize: true,
      yaxis: {
        automargin: true,
      },
    };
    Plotly.plot(graphDiv, graphData, layout);
  }

  /**
   * Fetches the data to plot the graph
   * @returns
   */
  visualiseCluster() {
    if (this.selectedFeature.length == 0) {
      this.showSnackBar('Please select at least one features', 'OK');
      return;
    } else if (this.selectedFeature.length > 2) {
      this.showSnackBar('Please select only two features', 'OK');
      return;
    }
    let analysisObj = [];
    if (this.clusterErrorType !== 'string') {
      this.model.modelMetaData.analysisInfo.map((element) => {
        let a = {
          clusterId: element.clusterId,
          clusterName: element.clusterName,
        };
        analysisObj.push(a);
      });
    }
    let tempFeature = [];
    this.selectedFeature.map((item) => {
      tempFeature.push(item.colName);
    });
    this._spinner.showSpinner();
    let reqObj: any = {
      algoName: this.model.modelMetaData.algoName,
      selectedFeatures: tempFeature ? tempFeature : null,
      indexPath: this.training.edaId.indexPath,
      transformedDatasetPath: this.model.transformedDatasetPath,
      modelPath: this.model.modelMetaData.modelPath,
      trainPipeFileName: this.model.trainPipeFilePath,
      rootFileName: this.originalFileWithEdaDetails.filename,
    };
    if (this.selectedFeature.length === 1) {
      reqObj.graphType = 'featureDistribution';
      reqObj.analysisInfo = analysisObj.length ? analysisObj : 'none';
      reqObj.fileSchema = this.originalFileWithEdaDetails.fileSchema;
    } else {
      reqObj.graphType = 'scatter';
    }
    if (this.selectedCluster) {
      reqObj.selectedCluster = this.selectedCluster;
      reqObj.graphType = 'scatterText';
    }
    // API call
    this._models
      .visualiseGraph(this.training._id, this.model._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (typeof res.graphContent !== 'string') {
            this.showSpinner = true;
            this.viewScatterPlot = false;
            let graphDiv: any = document.getElementById(this.uniqueGraphDiv);
            res.graphContent.layout.template.layout.title.xanchor = 'center';
            res.graphContent.layout.template.layout.title.y = 0.9;
            res.graphContent.layout.template.layout.title.x = 0.5;
            res.graphContent.layout.template.layout.title.yanchor = 'top';
            res.graphContent.layout.template.layout.xaxis.gridcolor = 'transparent';
            res.graphContent.layout.template.layout.yaxis.gridcolor = 'transparent';
            res.graphContent.layout.template.layout.yaxis.zerolinecolor = 'transparent';
            res.graphContent.layout.xaxis.linecolor = 'black';
            res.graphContent.layout.yaxis.linecolor = 'black';
            Plotly.newPlot(graphDiv, res.graphContent.data, res.graphContent.layout);
            this._spinner.stopSpinner();
          } else if (res.graphContent.includes('.html')) {
            // this._spinner.showSpinner();
            this.openEdaReport({ projectId: this.project._id, fileName: res.graphContent });
          } else {
            this._spinner.stopSpinner();
            this._toaster.error(res.graphContent, 'Model');
          }
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.message, 'Model');
        }
      );
  }

  /**
   * Update cluster name in model collection
   */
  updateClusterData() {
    this._spinner.showSpinner();
    let analysisObj = [];
    this.model.modelMetaData.analysisInfo.map((element) => {
      let a = {
        clusterId: element.clusterId,
        clusterName: element.clusterName,
      };
      analysisObj.push(a);
    });
    let reqObj = {
      analysisMap: analysisObj,
      modelMetaData: this.model.modelMetaData,
      modelId: this.model._id,
    };
    this._models
      .updateModel(reqObj, this.model._id)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          this.trainings[0].models = this.trainings[0].models.map((data: any) => {
            if (data._id === res._id) {
              data.modelMetaData.analysisInfo = res.modelMetaData.analysisInfo;
              data.modelMetaData.modelPath = res.modelMetaData.modelPath;
              data.showUpdateButton = false;
            }
            return data;
          });
          this.updateTrainingModels.emit(this.trainings[0].models);
          this.showUpdateButton = false;
          this._spinner.stopSpinner();
        },
        (err: Error) => {
          this._spinner.stopSpinner();
          this._toaster.error(err.message, 'Model');
        }
      );
  }

  showSummary() {
    this.hideSummary = false;
    this.hideHeatMap = true;
  }
  /**
   *  function to plot heat map for the clustering
   */
  plotHeatMap(): void {
    let graphDiv: any = document.getElementById(this.heatMapDiv);
    this._spinner.showSpinner();
    this.hideSummary = true;
    this.hideHeatMap = false;
    let analysisObj = [];
    this.model.modelMetaData.analysisInfo.map((element) => {
      let a = {
        clusterId: element.clusterId,
        clusterName: element.clusterName,
      };
      analysisObj.push(a);
    });
    let reqObj = {
      analysisInfo: analysisObj,
      clusterStats: this.model.modelMetaData.clusterStats,
    };
    this._models
      .clusterHeatMap(this.training._id, this.model._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          Plotly.newPlot(graphDiv, res.graphContent.data, res.graphContent.layout);
          this._spinner.stopSpinner();
        },
        (err: Error) => {
          this._toaster.error(err?.message || 'Cannot generate heat map', 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * Decision tree plot
   */
  plotDecisionTreePlot(): void {
    this._spinner.showSpinner();
    let analysisObj = [];
    this.model.modelMetaData.analysisInfo.map((element) => {
      let a = {
        clusterId: element.clusterId,
        clusterName: element.clusterName,
      };
      analysisObj.push(a);
    });
    let reqObj = {
      analysisInfo: analysisObj,
      treeModelOutput: this.model.modelMetaData.treeModelOutput,
    };
    this._models
      .clusterDecisionTree(this.training._id, this.model._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.showSpinner = true;
            const base64 = `data:image/jpg;base64,${res.graphContent}`;
            this.decisionTreeImage = this._sanitizer.bypassSecurityTrustResourceUrl(base64);
            this._spinner.stopSpinner();
          }
        },
        (err: Error) => {
          this._toaster.error(err?.message || 'Cannot generate decision tree graph', 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * download decision tree
   */
  downloadDecisionTree(): void {
    this._spinner.showSpinner();
    let analysisObj = [];
    this.model.modelMetaData.analysisInfo.map((element) => {
      let a = {
        clusterId: element.clusterId,
        clusterName: element.clusterName,
      };
      analysisObj.push(a);
    });
    let reqObj = {
      analysisInfo: analysisObj,
      operation: 'download',
      treeModelOutput: this.model.modelMetaData.treeModelOutput,
    };
    // API call
    this._models
      .clusterDecisionTree(this.training._id, this.model._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          this._spinner.stopSpinner();
          if (res.graphContent) {
            window.open(
              `${environment.serverBaseUrl}/api/projects/${this.model.projectId}/decisionTree/download/${res.graphContent}`,
              '_blank'
            );
          } else {
            this._toaster.error(res.graphContent || 'Cannot download decision tree', 'Models');
          }
        },
        (err: HttpErrorResponse) => {
          this._toaster.error(err.error?.message || 'Cannot download decision tree', 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * function to toggle between visualization
   */
  togglePlot(): void {
    if (this.model.modelMetaData.clusteringGraphType === 'featureImp') {
      this.viewType = 'graph';
      this.plotFeatureImp();
    } else if (this.model.modelMetaData.clusteringGraphType === 'scatter') {
      this.viewScatterPlot = true;
    } else if (this.model.modelMetaData.clusteringGraphType === 'decisionTree') {
      this.plotDecisionTreePlot();
    }
  }

  /**
   * function for hide/show button
   */
  onFocusOut(editedItem, index) {
    if (editedItem[0] === ' ') {
      this.showSnackBar('Cluster name cannot contain space', 'OK');
      return;
    }
    if (editedItem === '') {
      this.showSnackBar('Cluster name cannot be blank', 'OK');
      return;
    }
    if (editedItem.length > 20) {
      this.showSnackBar('Cluster name max length is 20 characters', 'OK');
      return;
    }
    let form = /^[^\s][A-Za-z0-9_\- ]+$/;
    if (form.test(editedItem) === false) {
      this.showSnackBar('Invalid cluster name.Only _ and - are supported', 'OK');
      return;
    }
    if (typeof +editedItem === 'number' && !isNaN(+editedItem)) {
      this.showSnackBar('Cluster name can not have only numeric value', 'OK');
      return;
    }
    let clusterName = this.model.modelMetaData.analysisInfo.filter((item) => {
      return item.clusterName === editedItem;
    });
    if (clusterName.length > 0) {
      this.model.showUpdateButton = false;
      this.showSnackBar('Cluster name cannot be duplicate', 'OK');
    } else {
      this.model.modelMetaData.analysisInfo[index].clusterName = editedItem;
      this.model.showUpdateButton = true;
    }
  }

  viewClusterFaq(clusterTemplate: TemplateRef<any>, Data?: any) {
    this._spinner.showSpinner();
    this.dialogRef = this._dialog.open(clusterTemplate, {
      // height: 'auto',
      width: '80%',
    });
    this._spinner.stopSpinner();
  }

  /**
   * Closes the opened modal by sending the data or without data
   */
  closeModal(data?: any): void {
    this.dialogRef.close(data);
  }
  /**
   * If the report type is EDA, then preview the EDA report in modal
   * @param reportData
   */
  openEdaReport(edaReportData: any): void {
    /** Opens the eda report component modal to render report */
    this._dialog
      .open(EdaReportComponent, {
        data: {
          moduleName: 'cluster',
          edaReportData: edaReportData,
        },
        maxHeight: '95vh',
        minWidth: '95vw',
      })
      .afterOpened()
      .subscribe((any) => {
        this._spinner.stopSpinner();
      });
  }

  /**
   * Checks the value is number or string and returns booleans
   * @param val
   * @returns
   */
  checkIsNumber = (val: any) => (typeof val === 'string' ? false : true);

  /**
   * Renders the snackbar
   */
  showSnackBar(message: string, button: string): void {
    this.snackBarRef = this._snackBar.open(message, button, {
      horizontalPosition: 'right',
      verticalPosition: 'bottom',
    });
  }

  /**
   * Function to sort an array of objects as per the key
   * @param {*} items input data - array
   * @param {*} key key for comparison
   */
  sortArryOFObjects(items: any, key: any): any {
    items.sort((a: any, b: any) => {
      return b[key] - a[key];
    });
    return items;
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
    if (this.dataSource) {
      this.dataSource.disconnect();
    }
  }
}
