  showDriftAnalysis: boolean = false;
  isImported: boolean = false;
  disableBO: boolean = false;
  appType: string = environment.appType;
  allowedSettingsOption: Array<any> = null;
  /** Variables with default values */
  retrainType: string = 'refit';
  sourceType: string = 'file';
  backGround: string = '#ffffff';
  foreGround: string = '#08509d';
  mergeDataset: boolean = false;

  dialogHeading: string;
