import { FormControl } from '@angular/forms';
import { environment } from 'src/environments/environment';
import { MatSnackBar } from '@angular/material/snack-bar';
import { SocketIoService } from 'src/app/core/socket-io.service';
import { ModelsService } from './../models.service';
import { DataService } from './../../data/data.service';
import { SpinnerService } from './../../../shared/spinner.service';
import { LocalStorageService } from './../../../shared/local-storage.service';
import { Component, Inject, OnDestroy, OnInit, OnChanges } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { TextAnalysisService } from '../../text-analysis/text-analysis.service';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { ModelsComponent } from '../models.component';
import { GridApi, GridOptions } from 'ag-grid-community';
import { GraphsDialogHandlerComponent } from '../graphs-dialog-handler/graphs-dialog-handler.component';
import { UtilsService } from 'src/app/shared/utils.service';

declare var $: any;
@Component({
  selector: 'app-quick-prediction',
  templateUrl: './quick-prediction.component.html',
  styleUrls: ['./quick-prediction.component.scss'],
})
export class QuickPredictionComponent implements OnInit, OnDestroy, OnChanges {
  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  /** Angular grid settings */
  gridOptions: GridOptions;
  $win = $(window);
  gridApi: GridApi;
  /** Variables with default values */
  predMode: string = 'file_upload';
  toggleView: boolean = false;
  fileToUpload: any;

  fileUploadControl = new FormControl('');
  fileEncoding: any;
  project: any;
  modelToPredict: any;
  predictionResult: any;
  originalFileDetails: any;
  quickPredformData: any = {};
  taFeatureConfig: Array<any> = [];
  quickPredUIMetaData: Array<any> = [];
  predictionPreviewData: Array<any> = [];
  predictionUploadResult: any;
  rowId: number;
  //configure it to change the file size - this is in mb.
  allowedPSFileUploadSize: number;
  // restriction variables
  appType: string = environment.appType;
  allowedModelOptions: Array<any> = null;
  user: any;
  gridColumnApi: any;
  selectedModel: any;
  models: any;
  ALGORITHMS_ARRAY: Array<any>;

  constructor(
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _data: DataService,
    private _textAnalysis: TextAnalysisService,
    private _localStorage: LocalStorageService,
    private _toaster: ToastrService,
    private _spinner: SpinnerService,
    private _modelsDialogRef: MatDialogRef<ModelsComponent>,
    private _models: ModelsService,
    private _socket: SocketIoService,
    private _snackbar: MatSnackBar,
    private _window: Window,
    private _dialog: MatDialog,
    private _utils: UtilsService
  ) {}

  async ngOnInit(): Promise<any> {
    this._spinner.showSpinner();
    this.ALGORITHMS_ARRAY = await this._utils.getAlgorithms();
    this.fileEncoding = 'utf_8';
    this.initQuickPredictionSocket();
    this.project = this._localStorage.getItem('project');
    //fetching the restriction details from localstorage user.
    this.user = this._localStorage.getItem('user');
    this.allowedPSFileUploadSize = this.user?.psFeatures?.allowedPSFileUploadSize || 50;
    this.models = this.dialogData.trainings[0].models;
    this.models = this.models.filter((item) => {
      console.log("m item",item)
      return item?.graphSupported?.status === true && item?.graphSupported?.isDeleted === false;
    });
    console.log(this.models)
    this.allowedModelOptions = this.user?.psFeatures?.allowedFeaturesList || [];
    await this.initializeQuickPrediction();
  }

  ngOnChanges() {}

  /**
   * Initializes the quick prediction required data
   */
  async initializeQuickPrediction(): Promise<any> {
    // this.predictionStarted = false;
    // API call
    this.originalFileDetails = await this.getMyFileData(
      this.project._id,
      this.dialogData.currentModelMeta.originalFileId
    ).catch((e: any) => {
      this._toaster.error('Could not fetch required information for Quick Prediction.', 'Models');
      this._spinner.stopSpinner();
      this.closeModal();
    });
    if (this.originalFileDetails?.taExperimentId) {
      // If original file has features generated using textanalysis, fetch and keep in local variable.
      // Later exclude these new features in qp form
      let taExperiment: any = await this.getTaExperiment(
        this.originalFileDetails.taExperimentId
      ).catch((e: any) => {
        this._toaster.error(`Error : ${e.error.message}`, 'Models');
        this._spinner.stopSpinner();
      });
      this.taFeatureConfig = taExperiment.taFeatureConfig || [];
    }
    this._spinner.stopSpinner();
  }

  onChangeDataset(dataset) {
    this.selectedModel = dataset.modelMetaData.algoName;
    this.predMode = 'file_upload';
    this.predictionResult = null;
    this.quickPredUIMetaData = [];
  }
  /**
   * Function to toggle/select the quick prediction mode using any one option
   * @param {*} mode file upload/input form
   */
  selectPredMode(mode: string): void {
    this.predictionPreviewData = [];
    this.predictionResult = null;
    this.quickPredUIMetaData = [];
    if (mode === 'input_form') {
      this._spinner.showSpinner();
      let reqObj: any = {
        indepVariable: this.dialogData.trainings[0].indepVariable,
        notebookInputFileName: this.dialogData.notebookInputFileDetails
          ? this.dialogData.notebookInputFileDetails.filename
          : '',
      };
      // Add ta details if taFeatureConfig is present
      if (this.taFeatureConfig.length) {
        reqObj['taFeatureConfig'] = this.taFeatureConfig;
        reqObj['textAnalysisOutFilePath'] = this.originalFileDetails.filename;
      }
      // API call
      this._models
        .getQuickPredictionFormData(
          this.dialogData.trainings[0]._id,
          this.modelToPredict._id,
          this.project.type,
          reqObj
        )
        .pipe(takeUntil(this.unsubscribe$))
        .subscribe(
          (res) => {
            if (res) {
              let formArray: Array<any> = res.qpFormData;
              formArray.forEach((qpData: any) => {
                if (this.originalFileDetails.notebookInputFileId || this.taFeatureConfig.length) {
                  if (this.dialogData.trainings[0].depVariable !== qpData.colName) {
                    this.quickPredUIMetaData.push({
                      colName: qpData.colName,
                      dataType: qpData.dataType,
                      value: qpData.value,
                    });
                  }
                } else {
                  if (this.itemExist(this.dialogData.trainings[0].indepVariable, qpData.colName)) {
                    this.quickPredUIMetaData.push({
                      colName: qpData.colName,
                      dataType: qpData.dataType,
                      value: qpData.value,
                    });
                  }
                }
                this.quickPredUIMetaData.forEach((item) => {
                  this.quickPredformData[item.colName] = item.value;
                });
              });
              this._spinner.stopSpinner();
            } else {
              this._toaster.error('Sorry! Cannot fetch the QP form data.', 'Models');
              this._spinner.stopSpinner();
            }
          },
          (err: any) => {
            this._spinner.stopSpinner();
            this._toaster.error(`Server Error - ${err.error.message}`, 'Models');
          }
        );
    } else {
      this.fileToUpload = '';
    }
  }

  /**
   * Generates the quick prediction by triggering API calls
   */
  async doPrediction(): Promise<any> {
    // this.predictionStarted = true;
    this.predictionResult = null;
    let reqObj: any;
    let algoName = this.modelToPredict.modelMetaData.algoName.split('-')[0];
    let algoType: any = this.ALGORITHMS_ARRAY.filter((a: any) => a.name === algoName)[0]?.type;
    // this.predictionFile = null;
    reqObj = {
      modelFilePath: this.modelToPredict.modelMetaData.modelPath,
      algoName: algoName,
      algoType: algoType,
      modelName: `${this.dialogData.trainings[0].name} - ${algoName}`,
      modelId: this.modelToPredict._id,
      trainingId: this.dialogData.trainings[0]._id,
      isMultilabel: this.modelToPredict.modelMetaData.isMultilabel,
      scalerObj: this.dialogData.trainings[0].scalerObj,
      reTrainFileId: this.modelToPredict.modelMetaData.reTrainFileId,
    };
    if (this.modelToPredict.hasOwnProperty('edaId') && this.modelToPredict.edaId !== null) {
      reqObj.edaId = this.modelToPredict.edaId;
    } else {
      reqObj.edaId = this.dialogData.trainings[0].edaId._id;
    }
    if (this.dialogData.trainings[0].logTransformationColumns) {
      reqObj.logTransformationColumns = this.dialogData.trainings[0].logTransformationColumns;
      reqObj.scalarValue = this.dialogData.trainings[0].scalarValue;
    }
    if (this.predMode == 'file_upload') {
      if (this.fileToUpload) {
        this._spinner.showSpinner();
        this.predictionUploadResult = await this._models
          .fileUploadPrediction(this.fileToUpload, this.project._id, this.fileEncoding)
          .pipe(takeUntil(this.unsubscribe$))
          .toPromise()
          .catch((e: any) => {
            this._toaster.error(`Server Error - ${e.error.message}.`, 'Models');
            this._spinner.stopSpinner();
          });
        if (this.predictionUploadResult.projectDetails) {
          reqObj.predictionFile = this.predictionUploadResult.projectDetails;
          // API call
          this.performPrediction(reqObj);
        } else {
          this._toaster.show('Sorry! Cannot proceed with the uploaded file.', 'Models');
          this._spinner.stopSpinner();
        }
      } else {
        this._snackbar.open('Please select a .csv file for prediction', 'OK', {
          horizontalPosition: 'right',
          verticalPosition: 'bottom',
        });
        this._spinner.stopSpinner();
      }
    } else {
      if (Object.keys(this.quickPredformData).length) {
        this._spinner.showSpinner();
        this.quickPredUIMetaData.forEach((data) => {
          this.quickPredformData[data.colName] = this.quickPredformData.hasOwnProperty(data.colName)
            ? this.quickPredformData[data.colName]
            : null;
        });
        if (this.quickPredformData['undefined']) {
          delete this.quickPredformData['undefined'];
        }
        reqObj.predictionData = this.quickPredformData;
        // API call
        this.performPrediction(reqObj);
      } else {
        // this.predictionStarted = false;
        this._snackbar.open('Please input data for prediction.', 'OK', {
          horizontalPosition: 'right',
          verticalPosition: 'bottom',
        });
        this._spinner.stopSpinner();
      }
    }
  }

  /**
   * Calls the quick prediction API to execute
   * @param reqObj
   */
  performPrediction(reqObj: any): void {
    this._models
      .doPrediction(this.dialogData.trainings[0]._id, this.modelToPredict._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (res) {
            this._toaster.info('Quick prediction started...', 'Models');
          } else {
            this._toaster.info('Sorry! Cannot execute the quick prediction.', 'Models');
            this._spinner.stopSpinner();
          }
        },
        (err: any) => {
          this._toaster.info(`Server Error - ${err.error.message}`, 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * Fetchs my file details
   * @param projectId
   * @param dataId
   */
  getMyFileData(projectId: string, dataId: string): Promise<any> {
    return this._data
      .getMyFileData(projectId, dataId)
      .pipe(takeUntil(this.unsubscribe$))
      .toPromise();
  }

  /**
   * Fetchs text-analysis experiments
   * @param projectId
   * @param originalFileId
   */
  getTaExperiment(experimentId: string): Promise<any> {
    return this._textAnalysis
      .taExperiment(experimentId)
      .pipe(takeUntil(this.unsubscribe$))
      .toPromise();
  }

  /**
   * Handles the dynamic graph dialog as per the type and opens it
   * @param type
   * @param trainingInfo
   * @param modelInfo
   */
  openGraphDialog(type: string, modelInfo: any, predMode: string): void {
    this._dialog
      .open(GraphsDialogHandlerComponent, {
        minWidth: '50vw',
        maxWidth: '50vw',
        data: {
          type: type,
          modelInfo: modelInfo,
          predMode: predMode,
          trainings: this.dialogData.trainings,
          rowId: this.rowId,
          predictionFile: this.predictionUploadResult?.projectDetails,
          quickPredformData: this.quickPredformData,
          originalFileWithEdaDetails: this.dialogData.originalFileWithEdaDetails,
        },
      })
      .afterClosed()
      .subscribe((data) => {});
  }

  /**
   * Filters the array and returns the item if exists
   * @param {*} arrayData input array
   * @param {*} item item to be searched
   */
  itemExist(arrayData: any, item: any): any {
    return arrayData.some((data: any) => {
      return data.colName === item;
    });
  }

  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any, fileEncoding) {
    const ext = inputFile[0].name.split('.').pop();
    if (inputFile && ['csv'].includes(ext)) {
      if (inputFile[0].size < this.allowedPSFileUploadSize * 1024 * 1024) {
        this.fileToUpload = inputFile[0];
      } else {
        this._snackbar.open(
          'File size should be less than ' + this.allowedPSFileUploadSize + 'Mb.',
          'OK',
          {
            horizontalPosition: 'right',
            verticalPosition: 'bottom',
          }
        );
      }
    } else {
      this.fileToUpload = '';
      this._snackbar.open(`.${ext} file type is not supported`, 'OK');
    }
  }

  /**
   * Downloads the quick prediction result
   */
  downloadPredictedFile(): void {
    this._window.open(
      `${environment.serverBaseUrl}/api/prediction/${this.predictionResult._id}/download`,
      '_blank'
    );
  }

  /**
   * Called when ng-grid is rendered in dom
   * @param event
   */
  gridReady(event: any) {
    event.api.setDomLayout('autoHeight');
    this.gridApi = event.api;
    this.gridApi.refreshCells({ force: true });
    this.gridApi.setColumnDefs(this.gridOptions.columnDefs);
    this.gridApi.setRowData(this.gridOptions.rowData);
  }

  /**
   * Closes the current opened dialog
   */
  closeModal(data?: any): void {
    this._modelsDialogRef.close(data);
  }

  /**
   * SOCKET - Listen for prediction result when prediction is completed
   */
  initQuickPredictionSocket() {
    this._socket.listen('predictionCompleted').subscribe(
      (data: any) => {
        if (data.status == 'failed') {
          this._toaster.error(data.data.message, 'Models');
          // this.predictionStarted = false;
          this.predictionUploadResult = null;
          this._spinner.stopSpinner();
        }
        if (data.predictionFileMetaData) {
          this.predictionResult = data;
          // this.predictionStarted = false;
          // API call
          this._models
            .generatePreviewTable(this.predictionResult._id)
            .pipe(takeUntil(this.unsubscribe$))
            .subscribe(
              (res) => {
                if (res) {
                  this.predictionPreviewData = [];
                  let columnDefs: Array<any> = [];
                  let firstRecord = res[0];
                  for (let key in firstRecord) {
                    if (firstRecord.hasOwnProperty(key)) {
                      columnDefs.push({
                        headerName: key,
                        field: key,
                        minWidth: 120,
                      });
                    }
                  }
                  this.predictionPreviewData = [...res];
                  this.gridOptions = {
                    headerHeight: 40,
                    columnDefs: columnDefs,
                    rowData: res,
                    defaultColDef: { resizable: true },
                  };
                  this._toaster.success('Quick prediction completed successfully', 'Models');
                  this._spinner.stopSpinner();
                } else {
                  this._spinner.stopSpinner();
                  this._toaster.error('Cannot preview the prediction result', 'Models');
                }
              },
              (err: any) => {
                this._spinner.stopSpinner();
                this._toaster.error(`Server Error - ${err.error.message}`, 'Models');
              }
            );
        }
      },
      (err: any) => {
        this._spinner.stopSpinner();
        this._toaster.error(`Socket Error - ${err.error.message}`, 'Models');
      }
    );
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this._socket.removeListener('predictionCompleted');
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
