import { Component, Input, OnInit, OnChanges } from '@angular/core';
import { LocalStorageService } from 'src/app/shared/local-storage.service';

@Component({
  selector: 'app-pipeline-expansion',
  templateUrl: './pipeline-expansion.component.html',
  styleUrls: ['./pipeline-expansion.component.scss'],
})
export class PipelineExpansionComponent implements OnChanges {
  @Input() pipelineDetails: any;
  @Input() selectedTask: any;
  project: any;
  taskData: any = [];
  columnList: any = [];
  method: any = [];
  shape: any = [];

  constructor(private _localStorage: LocalStorageService) { }

  ngOnChanges(): void {
    this.project = this._localStorage.getItem('project');
    this.taskData[this.selectedTask] = this.pipelineDetails['data'];
    this.columnList[this.selectedTask] = this.pipelineDetails['columnList'];
    this.method[this.selectedTask] = this.pipelineDetails['method'];
    this.shape[this.selectedTask] = this.pipelineDetails['shape'];
    // console.log(this.columnList[this.selectedTask].length);
  }

  /**
   * Check whether input data is array or not
   * @param {*} data
   */
  isArrayData(data: any): boolean {
    return Array.isArray(data);
  }
}
