  /**
   * Fetches the seleted training models
   */
  async showSelectedTrainingModels(selectedTraining: any): Promise<any> {
    // API call
    selectedTraining.models = await this.getModelsList(this.selectedTrainingControl.value).catch(
      (e: Error) => {
        this._toaster.error(`Error - ${e.message}`, 'Models');
        this._spinner.stopSpinner();
      }
    );
    this.trainings = [selectedTraining];
    // API call
    this.originalFileDetails = await this.getOriginalFileDetails(
      this.trainings[0].originalFileId,
      this.trainings[0].projectId
    ).catch((e: Error) => {
      this._toaster.error(`Error - ${e.message}`, 'Models');
      this._spinner.stopSpinner();
    });
    // API call
    if (this.originalFileDetails.notebookInputFileId) {
      this.notebookInputFileDetails = await this.getNotebookInputFileDetails(
        this.originalFileDetails.projectId,
        this.originalFileDetails.notebookInputFileId
      ).catch((e: Error) => {
        this._toaster.error(`Error - ${e.message}`, 'Models');
        this._spinner.stopSpinner();
      });
    }
    // API call
    let fileWithEda: any = await this.getChildDataSets(
      this.trainings[0].projectId,
      this.trainings[0].originalFileId
    ).catch((e: Error) => {
      this._toaster.error(`Error - ${e.message}`, 'Models');
      this._spinner.stopSpinner();
    });
    this.originalFileWithEdaDetails = fileWithEda[0];
    this.totalAlgos =
      this.trainings[0].algorithms.length <= this.trainings[0].models.length
        ? this.trainings[0].models.length
        : this.trainings[0].algorithms.length;
    this.totalAlgos < this.trainings[0].algorithms.length
      ? (this.showTrainingSpinner = true)
      : (this.showTrainingSpinner = false);
    if (this.trainings[0].edaId.isMultipleTimeseries) {
      this.rejectedGroupList = await this.getTimeseriesList(this.trainings[0], this.trainings[0].models[0]);
      this.rejectedGroupList = this.rejectedGroupList[0].rejectedGroupList;
    }
    this.sortAndRankModels(this.trainings[0]);
    this.supportedGraphList();
    // this.trainings[0].models.forEach(element => {
    this.isAdvanceAlgoSupported();
    // });
    this.trainings[0].problemType === 'clustering'
      ? this.genClusteringFeatures(this.trainings[0])
      : null;
    this._spinner.stopSpinner();
  }

  /**
   * Generate features for clustering
   * @param {*} trainingData
   */
  genClusteringFeatures(trainingData: any): void {
    this.clusteringFeatures = [];
    // If decomposition == 'pca' or factoranalysis, first priority
    if (trainingData.clusteringInfo.decomposition !== 'none') {
      //generate custom values for pca
      for (let i = 0; i < trainingData.clusteringInfo.components; i++) {
        this.clusteringFeatures.push('component_' + i);
      }
    } else {
      if (trainingData.clusteringInfo.numericFeatures.length && trainingData.indepVariable.length) {
        //Filter out numeric indepVariables
        this.clusteringFeatures = trainingData.clusteringInfo.numericFeatures.map((data: any) => {
          let found: any = trainingData.indepVariable.find(
            (indepVar: any) => indepVar.colName === data.colName
          );
          if (found) {
            return data.colName;
          }
        });
      }
    }
  }

