  /**
   * function to check supported graph for particular algo
   */
  supportedGraphList(): any {
    this.trainings[0].models.forEach((element) => {
      let string = element.modelMetaData.algoName.toLowerCase();
      let algoName = [];
      let subString1 = 'bagging';
      let subString2 = 'boosting';
      if (string.includes(subString1)) {
        if (this.trainings[0].algoType == 'classification') {
          algoName = ['Bagging Classifier'];
        } else if (this.trainings[0].algoType === 'regression') {
          algoName = ['Bagging Regressor'];
        }
        let graphSupported = this._utils.getAlgorithmSupported(algoName, this.trainings[0], string);
        if (graphSupported !== null) {
          element['graphSupported'] = graphSupported;
        }
      } else if (string.includes(subString2)) {
        if (this.trainings[0].algoType == 'classification') {
          algoName = ['Boosting Classifier'];
        } else if (this.trainings[0].algoType === 'regression') {
          algoName = ['Boosting Regressor'];
        }
        let graphSupported = this._utils.getAlgorithmSupported(algoName, this.trainings[0], string);
        if (graphSupported !== null) {
          element['graphSupported'] = graphSupported;
        }
      } else {
        let modelName = element.modelMetaData.algoName;
        modelName = modelName.split('-', 1);
        let graphSupported = modelName.length
          ? this._utils.getAlgorithmSupported(modelName, this.trainings[0], string)
          : this._utils.getAlgorithmSupported(
              element.modelMetaData.algoName,
              this.trainings[0],
              string
            );
        if (graphSupported !== null) {
          element['graphSupported'] = graphSupported;
        }
      }
    });
    this.modelSupported = this.trainings[0].models.filter((item) => {
      return item.graphSupported.status === true;
    });
   // console.log(this.trainings);
  }

  /**
   * function to disable bagging and boosting for already bagged and boosted algo
   */
  isAdvanceAlgoSupported() {
    this.trainings[0].models.forEach((model: any) => {
      let string = model.modelMetaData.algoName.toLowerCase();
      let subString1 = 'bagging';
      let subString2 = 'boosting';

      if (string.includes(subString1)) {
        model['isAlgoSupported'] = false;
      } else if (string.includes(subString2)) {
        model['isAlgoSupported'] = false;
      } else {
        model['isAlgoSupported'] = true;
      }
    });
  }
  /**
   * Sorts the models and rank them as per the score
   * @param training
   */
  sortAndRankModels(training: any): void {
    /**
     * Sorting is required only for predictive_modeling problem type
     */
    if (this.project.type !== 'predictive_modeling') {
      return null;
    }
    this.values.length = 0;
    training.models.sort((a: any, b: any) => {
      if (
        this.filter[0] === 'Mean Absolute Error' ||
        this.filter[0] === 'Root Mean Square Error' ||
        this.filter[0] === 'Median Square Error' ||
        this.filter[0] === 'Mean Square Error' ||
        this.filter[0] === 'Mean Square Log Error'
      ) {
        let aa = a.modelMetaData.holdout.losses.filter((pilot: any) => {
          return pilot.name === this.filter[0];
        });
        let bb = b.modelMetaData.holdout.losses.filter((pilot: any) => {
          return pilot.name === this.filter[0];
        });
        if (this.filterSortOrder === 'desc') {
          return bb[0].value * 1 - aa[0].value * 1;
        } else {
          return aa[0].value * 1 - bb[0].value * 1;
        }
      }
      /**
       * Sorting the models in desc/asc order based on the selected filter
       */
      if (this.filterSortOrder === 'desc') {
        if (this.filter[0] === 'timeElapsed') {
          return b.modelMetaData[this.filter[0]] * 1 - a.modelMetaData[this.filter[0]] * 1;
        } else {
          return (
            b.modelMetaData.holdout[this.filter[0]] * 1 -
            a.modelMetaData.holdout[this.filter[0]] * 1
          );
        }
      } else {
        if (this.filter[0] === 'timeElapsed') {
          return a.modelMetaData[this.filter[0]] * 1 - b.modelMetaData[this.filter[0]] * 1;
        } else {
          return (
            a.modelMetaData.holdout[this.filter[0]] * 1 -
            b.modelMetaData.holdout[this.filter[0]] * 1
          );
        }
      }
    });

    training.models.forEach((data: any) => {
      if (data.modelMetaData.holdout) {
        data.modelMetaData.overallScore = data.modelMetaData.holdout.overallScore;
        this.values.push({
          label: training.name + ' - ' + data.modelMetaData.algoName,
          value: data.modelMetaData.overallScore * 1,
        });
      }
    });
    this.data[0].values = this.values;
    this.initModels(this.project.type, training.models);
  }

