  /**
   * Stores the file data to be uploaded into the variable
   * @param inputFile files object
   */
  handleFileInput(inputFile: any, fileEncoding) {
    const ext = inputFile[0].name.split('.').pop();
    if (inputFile && ['csv'].includes(ext)) {
      if (inputFile[0].size < this.allowedPSFileUploadSize * 1024 * 1024) {
        this.fileToUpload = inputFile[0];
      } else {
        this._snackBar.open(
          'File size should be less than ' + this.allowedPSFileUploadSize + 'Mb',
          'OK',
          {
            horizontalPosition: 'right',
            verticalPosition: 'bottom',
          }
        );
      }
    } else {
      this.fileToUpload = '';
      this._snackbar.open(`.${ext} file type is not supported`, 'OK');
    }
  }
