  /**
   * Deploys the model
   */
  async deployModelFile(): Promise<any> {
    // API call
    this.resDeployfile = await this.deployModelService(this.deployObj).catch((e: any) => {
      this._toaster.error(e.error?.message || 'Model deployment failed', 'Models');
      this._spinner.stopSpinner();
    });
    if (this.resDeployfile) {
      this.modelDeployed = await this.deployModelPost(this.deployReqObj)
        .then((res) => {
          if (res) {
            this._toaster.success(res.message || 'Model is deployed sucessfully', 'Models');
          }
        })
        .catch((e: any) => {
          this._toaster.error(e.error.message || 'Model deployment failed', 'Models');
          this._spinner.stopSpinner();
        });
    }
  }

