  /**
   * Function to find algo type - regression/classification
   * @param {*} algoName - algo full name
   */
  findAlgoType(algoName: any): any {
    var algoSplit = algoName.split('-')[0];
    var algoType = this.ALGORITHMS_ARRAY.filter((algorithm: any) => algorithm.name === algoSplit)[0]
      .type;
    return algoType;
  }

