  /**
   * Opens the pipeline dialog
   */
  openPipelineDialog(): void {
    let autoGenerationData = this.trainings[0]['autoFeatureGenTasks'];
    this._models
      .getPipelineTask(this.currentModelMeta.trainingId)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        async (res) => {
          if (res) {
            if (
              this.trainings[0].sampling == 'under_sampling' ||
              this.trainings[0].sampling == 'over_sampling'
            ) {
              var obj = {
                description:
                  'Sampling is a method that allows us to get information about the population based on the statistics from a subset of the population (sample), without having to investigate every individual.',
                index: 6,
                parent: 'training_data_prep',
                preview: 'True',
                task: 'sampling',
                taskName: 'Sampling',
              };
              res.push(obj);
            }
            var dataCollection = [];
            var eda = [];
            var training = [];
            this.pipelineTasks = [];
            res.forEach(function (item) {
              if (item.parent == 'Data Collection') {
                dataCollection.push(item);
              }
              if (item.parent == 'EDA') {
                eda.push(item);
              }
              if (item.parent == 'training_data_prep') {
                if (item.task == 'auto_feature_generate') {
                  var tempAFoperations = [];
                  autoGenerationData.forEach(function (autoFGenOp) {
                    tempAFoperations.push(autoFGenOp['operation']);
                  });
                  item.afOperations = tempAFoperations;
                }
                training.push(item);
              }
            });
            this.pipelineTasks.push({
              data: dataCollection,
              parent: 'Data Collection',
            });
            this.pipelineTasks.push({ data: eda, parent: 'EDA' });
            this.pipelineTasks.push({ data: training, parent: 'Training' });
            this._dialog
              .open(PipelineComponent, {
                maxWidth: '90vw',
                data: {
                  trainings: this.trainings,
                  currentModelMeta: this.currentModelMeta,
                  notebookInputFileDetails: this.notebookInputFileDetails,
                  originalFileWithEdaDetails: this.originalFileWithEdaDetails,
                  pipelineTask: this.pipelineTasks,
                  pipelineData: res,
                },
              })
              .afterClosed()
              .subscribe((data) => {});
          }
        },
        (err) => {
          this._spinner.stopSpinner();
          this._toaster.error('Server Error', 'Models');
        }
      );
  }

