  /**
   * Function to sort an array of objects as per the key
   * @param {*} items input data - array
   * @param {*} key key for comparison
   */
  sortArryOFObjects(items: any, key: any): any {
    items.sort((a: any, b: any) => {
      return b[key] - a[key];
    });
    return items;
  }
  /**
   * Opens the new window with algorithm details and doc
   * @param {*} algoName
   */
  getAlgoDouUrl(algoName: string): void {
    let queryForUrl = null;
    if (algoName.includes('Bagging') && algoName.includes('Regression')) {
      if (algoName.includes('Logistic')) {
        queryForUrl = 'Bagging Classifier';
      } else {
        queryForUrl = 'Bagging Regressor';
      }
    } else if (algoName.includes('Bagging') && algoName.includes('Classification')) {
      queryForUrl = 'Bagging Classifier';
    } else if (algoName.includes('Boosting') && algoName.includes('Classification')) {
      queryForUrl = 'Boosting Classifier';
    } else if (algoName.includes('Boosting') && algoName.includes('Regression')) {
      if (algoName.includes('Logistic')) {
        queryForUrl = 'Boosting Classifier';
      } else {
        queryForUrl = 'Boosting Regressor';
      }
    } else {
      queryForUrl = algoName.split('-')[0];
    }
    if (queryForUrl) {
      let algo = this.ALGORITHMS_ARRAY.filter((algorithm: any) => algorithm.name === queryForUrl);
      this._window.open(algo[0].url, '_blank');
    }
  }

  /**
   * SOCKET - Listen for training completed events
   */
  initTrainingSocket(): void {
    this._socket.listen('TrainingProgress').subscribe(
      async (data: any) => {
        if (data) {
          if (!this.trainings[0]) {
            await this.getTrainingsList();
          }
          if (this.trainings[0]) {
            if (data.status === 'success') {
              this.trainings[0].models.push(data.modelMetaData);
              if (data.trainingType == 'retraining') {
                this.totalAlgos += 1;
                this.sortAndRankModels(this.trainings[0]);
                this._toaster.success(
                  `Retraining successfully completed for ${data.algoName}`,
                  'Models'
                );
              } else if (data.trainingType === 'model_tuning') {
                this.totalAlgos += 1;
                this.sortAndRankModels(this.trainings[0]);
                this._toaster.success(
                  `Model tuning successfully completed for ${data.modelMetaData.modelMetaData.algoName}`,
                  'Models'
                );
              } else if (data.trainingType === 'bagging_boosting') {
                this.totalAlgos += 1;
                this.sortAndRankModels(this.trainings[0]);
                this._toaster.success(
                  `Bagging/Boosting successfully completed for ${data.algoName}`,
                  'Models'
                );
              }
              this._spinner.stopSpinner();
            } else {
              if (data.trainingType === 'train') {
                this.erroCount += 1;
              } else if (data.trainingType == 'retraining') {
                this._toaster.error(
                  `Retraining failed for ${data.algoName} - ${data.modelMetaData.message}`,
                  'Models'
                );
              } else if (data.trainingType === 'model_tuning') {
                this._toaster.error(
                  `Model tuning failed for ${data.algoName} - ${data.modelMetaData.message}`,
                  'Models'
                );
              } else if (data.trainingType === 'bagging_boosting') {
                this._toaster.error(
                  `Bagging/Boosting failed for ${data.algoName} - ${data.modelMetaData.message}`,
                  'Models'
                );
              }
              this._spinner.stopSpinner();
            }
            if (data.trainingType === 'train') {
              if (
                this.trainings[0].algorithms.length ===
                this.erroCount + this.trainings[0].models.length
              ) {
                this.showTrainingSpinner = false;
                this.getTrainingsList();
              } else {
                this.showTrainingSpinner = true;
              }
            }
            this.supportedGraphList();
          }
          this._spinner.stopSpinner();
        } else {
          this._spinner.stopSpinner();
        }
      },
      (err: Error) => {
        this._spinner.stopSpinner();
        this._toaster.error(`Socket Error - ${err.message}`, 'Models');
      }
    );
  }

