import { MatDialog } from '@angular/material/dialog';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { UtilsService } from 'src/app/shared/utils.service';
import { GraphsDialogHandlerComponent } from '../graphs-dialog-handler/graphs-dialog-handler.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-expansion-graphs',
  templateUrl: './expansion-graphs.component.html',
  styleUrls: ['./expansion-graphs.component.scss'],
})
export class ExpansionGraphsComponent implements OnInit {
  @Input() training: any;
  @Input() model: any;
  @Input() activeTabName: string;
  @Input() originalFileWithEdaDetails: string;
  @Input() trainings: any;

  project: any;
  modelData: any;
  // restriction variables
  appType: string = environment.appType;
  allowedModelOptions: Array<any> = null;
  user: any;
  graphSupported: any;
  constructor(
    public _utils: UtilsService,
    private _localStorage: LocalStorageService,
    private _dialog: MatDialog
  ) { }

  ngOnInit(): void {
    //fetching the restriction details from localstorage user.
    this.user = this._localStorage.getItem("user");
    this.allowedModelOptions = this.user?.psFeatures?.allowedFeaturesList || [];
  }

  ngOnChanges(): void {
    this.project = this._localStorage.getItem('project');
    // Use the modelData to render the details as per the selected tab name
    if (this.model.modelMetaData.dev && this.activeTabName === 'Development') {
      this.modelData = this.model.modelMetaData.dev;
    } else if (this.model.modelMetaData.holdout && this.activeTabName === 'Hold Out') {
      this.modelData = this.model.modelMetaData.holdout;
    } else if (this.activeTabName === 'Scoring Result') {
      this.modelData = this.model.modelMetaData.scoringMetrics;
    }
  }

  /**
   * Handles the dynamic graph dialog as per the type and opens it
   * @param type
   * @param trainingInfo
   * @param modelInfo
   */
  openGraphDialog(
    type: string,
    trainingInfo?: any,
    modelInfo?: any,
    tab?: string,
    predMode?: string
  ): void {
    this._dialog
      .open(GraphsDialogHandlerComponent, {
        minWidth: type === 'featureImpact' || type === 'analysis' ? '80vw' : '60vw',
        maxWidth: type === 'limeReport' ? '60vw' : '0',
        data: {
          type: type,
          trainingInfo: trainingInfo,
          modelInfo: modelInfo,
          tab: tab,
          trainings: this.trainings,
          originalFileWithEdaDetails: this.originalFileWithEdaDetails,
          predMode: predMode,
        },
      })
      .afterClosed()
      .subscribe((data) => { });
  }
}
