import { LocalStorageService } from './../../../shared/local-storage.service';
import { MatDialog } from '@angular/material/dialog';
import { SocketIoService } from 'src/app/core/socket-io.service';
import { ToastrService } from 'ngx-toastr';
import { ModelsService } from './../models.service';
import { SpinnerService } from './../../../shared/spinner.service';
import { ColorsService } from './../../../shared/colors/colors.service';
import { UtilsService } from './../../../shared/utils.service';
import { Component, EventEmitter, Input, OnDestroy, OnInit, Output } from '@angular/core';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { GraphsDialogHandlerComponent } from '../graphs-dialog-handler/graphs-dialog-handler.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-predictive-expansion',
  templateUrl: './predictive-expansion.component.html',
  styleUrls: ['./predictive-expansion.component.scss'],
})
export class PredictiveExpansionComponent implements OnInit, OnDestroy {
  @Input() training: any;
  @Input() model: any;
  @Input() originalFileWithEdaDetails: any;
  @Input() trainings: any;
  @Output() updateTrainingModels = new EventEmitter<any>();

  /** Usubscription subject */
  unsubscribe$ = new Subject<boolean>();

  ALGORITHMS_ARRAY: Array<any>;

  /** EasyPie setting */
  pieOptions1: any = {
    animate: {
      duration: 1000,
      enabled: true,
    },
    barColor: this._colors.byName('info'),
    trackColor: '#f2f2f2',
    scaleColor: false,
    lineWidth: 8,
    size: 150,
    lineCap: 'round',
  };

  /** Variables with default values */
  backGround: string = '#ffffff';
  foreGround: string = '#08509d';
  showGraphs: boolean = true;
  activeTabName: string = 'Development';
  algoType: string;
  project: any;
  metric: any;
  isMultilabel: boolean = false;
  showSpinner: boolean = false;
  regressionMetrics: any = [];
  classificationMetrics: any = [];
  validationStrategy: any = {
    technique: 'kFold',
    nSplit: 5,
  };
  metrics: any;
  metricdata: any[];
  // restriction variables
  appType: string = environment.appType;
  allowedModelOptions: Array<any> = null;
  user: any;
  graphSupported: any;
  badgeText:string = '';

  constructor(
    public _utils: UtilsService,
    private _colors: ColorsService,
    private _spinner: SpinnerService,
    private _models: ModelsService,
    private _toaster: ToastrService,
    private _socket: SocketIoService,
    private _dialog: MatDialog,
    private _localStorage: LocalStorageService
  ) {
    this.regressionMetrics = [
      {
        name: 'R Squared',
        value: 'r2',
        Name: 'R-Squared',
      },
      {
        name: 'MAE',
        value: 'neg_mean_absolute_error',
        Name: 'Mean absolute error',
      },
      {
        name: 'MSE',
        value: 'neg_mean_squared_error',
        Name: 'Mean squared error',
      },
      {
        name: 'RMSE',
        value: 'neg_root_mean_squared_error',
        Name: 'Root mean squared error',
      },
    ];
    this.classificationMetrics = [
      {
        Name: 'Acurracy',
        value: 'accuracy',
      },
      {
        Name: 'f1 Score',
        value: 'f1',
      },
      {
        Name: 'Precision',
        value: 'precision',
      },
      {
        Name: 'Recall',
        value: 'recall',
      },
    ];
  }

  async ngOnInit(): Promise<any> {
    this.ALGORITHMS_ARRAY = await this._utils.getAlgorithms();
    this.graphListSupported();
    this.initCvSocket();
    let algo: any = this.model.modelMetaData.algoName.split('-');
    this.project = this._localStorage.getItem('project');
    //fetching the restriction details from localstorage user.
    this.user = this._localStorage.getItem('user');
    this.allowedModelOptions = this.user?.psFeatures?.allowedFeaturesList || [];
    this.allowedModelOptions.includes('crossValidation') ? this.badgeText = '' : this.badgeText = 'PRO';
    this.algoType = this.ALGORITHMS_ARRAY.filter((a: any) => a.name === algo[0])[0]?.type;
    this.isMultilabel = this.training.isMultilabel;
    if (this.model.modelMetaData.holdout) {
      this.activeTabName = 'Hold Out';
    }
    if (this.algoType == 'regression') {
      this.metrics = this.regressionMetrics;
      this.metric = 'neg_mean_squared_error';
      this.validationStrategy.technique = 'kFold';
      if (this.model.modelMetaData.cvData?.status === 'done') {
        this.metricdata = [];
        this.metricdata = this.regressionMetrics.filter((item) => {
          return item.value == this.model.validationStrategy.metric;
        });
      }
    } else if (this.algoType == 'classification') {
      this.metrics = this.classificationMetrics;
      if (this.isMultilabel) {
        this.metrics = this.classificationMetrics.filter((item) => {
          return item.value == 'accuracy' || item.value == 'f1';
        });
        this.validationStrategy.technique = 'kFold';
      } else {
        this.validationStrategy.technique = 'stratifiedKFold';
      }
      this.metric = 'accuracy';
    }
    if (this.model.validationStrategy) {
      this.metric = this.model.validationStrategy.metric;
      this.validationStrategy.technique = this.model.validationStrategy.technique;
      this.validationStrategy.nSplit = this.model.validationStrategy.nSplit;
    }
  }

  graphListSupported() {
    let string = this.model.modelMetaData.algoName.toLowerCase();
    let algoName = [];
    let subString1 = 'bagging';
    let subString2 = 'boosting';
    if (string.includes(subString1)) {
      if (this.training.algoType == 'classification') {
        algoName = ['Bagging Classifier'];
      } else if (this.training.algoType === 'regression') {
        algoName = ['Bagging Regressor'];
      }
      this.graphSupported = this._utils.getAlgorithmSupported(algoName, this.training, string);
      this.model['graphSupported'] = this.graphSupported;
    } else if (string.includes(subString2)) {
      if (this.training.algoType == 'classification') {
        algoName = ['Boosting Classifier'];
      } else if (this.training.algoType === 'regression') {
        algoName = ['Boosting Regressor'];
      }
      this.graphSupported = this._utils.getAlgorithmSupported(algoName, this.training, string);
      this.model['graphSupported'] = this.graphSupported;
    } else {
      let modelName = this.model.modelMetaData.algoName;
      modelName = modelName.split('-', 1);
      this.graphSupported = this._utils.getAlgorithmSupported(modelName, this.training, string);
      this.model['graphSupported'] = this.graphSupported;
    }
  }
  /**
   * Function which triggers hold out API and fetchs hold out reuslt
   * @param {*} training training details
   * @param {*} modelInfo model details
   */
  doHoldOut(training: any, modelInfo: any): void {
    this._spinner.showSpinner();
    let reqObj: any = {
      algoName: modelInfo.modelMetaData.algoName,
      algoType: this.algoType,
      backGround: this.backGround,
      foreGround: this.foreGround,
      reTrainFileId: modelInfo.modelMetaData.reTrainFileId,
      modelFilePath: modelInfo.modelMetaData.modelPath,
      edaSummary: this.originalFileWithEdaDetails.edaId.edaSummary,
    };
    if (training.logTransformationColumns) {
      reqObj.logTransformationColumns = training.logTransformationColumns;
      reqObj.scalarValue = training.scalarValue;
    }

    // API call
    this._models
      .performHoldOut(training._id, modelInfo._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.trainings[0].models = this.trainings[0].models.map((data: any) => {
              if (data._id === res._id) {
                data = res;
              }
              return data;
            });
            this.updateTrainingModels.emit(this.trainings[0].models);
            this._toaster.success('Hold out operation successfully completed', 'Models');
            this._spinner.stopSpinner();
          } else {
            this._toaster.error('Cannot generate distribution graph', 'Models');
            this._spinner.stopSpinner();
          }
        },
        (err: Error) => {
          this._toaster.error(`Server Error - ${err.message}`, 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * Function which triggers API to get CV score
   * @param {*} training training details
   * @param {*} modelInfo model details
   */
  runCV(training: any, modelInfo: any): void {
    this.showSpinner = true;
    this._toaster.info('CV score calculation started', 'Models');
    modelInfo.modelMetaData.cvData = {};
    modelInfo.modelMetaData.cvData.status = 'running';
    let edaId;
    if (modelInfo.edaId !== null) {
      edaId = modelInfo.edaId;
    } else {
      edaId = training.edaId._id;
    }
    // API call
    this._models
      .runCv(
        training._id,
        modelInfo._id,
        modelInfo.modelMetaData.algoName,
        this.algoType,
        this.validationStrategy.technique,
        this.validationStrategy.nSplit,
        this.metric,
        edaId,
        'running'
      )
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res) => {
          if (!res) {
            this._toaster.error('Cannot calculate CV score', 'Models');
            this.showSpinner = false;
          }
        },
        (err: Error) => {
          this._toaster.error(`Server Error - ${err.message}`, 'Models');
          this.showSpinner = false;
        }
      );
  }

  /**
   * Function which triggers scoring API, to get scoring result
   * @param {*} training training details
   * @param {*} modelInfo model details
   */
  doScoring(training: any, modelInfo: any): void {
    this._spinner.showSpinner();
    // checkThemeColor();
    let reqObj: any = {
      backGround: this.backGround,
      foreGround: this.foreGround,
      algoName: modelInfo.modelMetaData.algoName,
      algoType: this.algoType,
      modelFilePath: modelInfo.modelMetaData.modelPath,
      transformedDatasetPath: modelInfo.transformedDatasetPath,
      reTrainFileId: modelInfo.modelMetaData.reTrainFileId,
    };
    if (modelInfo.edaId !== null) {
      reqObj.edaId = modelInfo.edaId;
    } else {
      reqObj.edaId = this.trainings[0].edaId._id;
    }
    if (this.trainings[0].logTransformationColumns) {
      reqObj.logTransformationColumns = this.trainings[0].logTransformationColumns;
      reqObj.scalarValue = this.trainings[0].scalarValue;
    }
    // API call
    this._models
      .performScoring(training._id, modelInfo._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (res: any) => {
          if (res) {
            this.trainings[0].models = this.trainings[0].models.map((data: any) => {
              if (data._id === res._id) {
                data = res;
              }
              return data;
            });
            this.updateTrainingModels.emit(this.trainings[0].models);
            this._toaster.success('Scoring result calculated successfully', 'Models');
            this._spinner.stopSpinner();
          } else {
            this._toaster.error('Cannot calculate scoring result', 'Models');
            this._spinner.stopSpinner();
          }
        },
        (err: any) => {
          this._toaster.error(err.error.message, 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * Handles the dynamic graph dialog as per the type and opens it
   * @param type
   * @param trainingInfo
   * @param modelInfo
   */
  openGraphDialog(type: string, trainingInfo?: any, modelInfo?: any): void {
    this._dialog
      .open(GraphsDialogHandlerComponent, {
        minWidth: type === 'learningCurve' ? '50vw' : '80vw',
        data: {
          type: type,
          trainingInfo: trainingInfo,
          modelInfo: modelInfo,
          trainings: this.trainings,
        },
      })
      .afterClosed()
      .subscribe((data) => { });
  }

  /**
   * Renders the graphs section view as per the selected tab
   * @param event
   */
  showGraphsSection(event: any): void {
    if (event.tab.textLabel === 'Development') {
      this.showGraphs = true;
    } else if (event.tab.textLabel === 'Hold Out') {
      this.model.modelMetaData.holdout ? (this.showGraphs = true) : (this.showGraphs = false);
    } else if (event.tab.textLabel === 'Scoring Result') {
      this.model.modelMetaData.scoringMetrics
        ? (this.showGraphs = true)
        : (this.showGraphs = false);
    } else {
      this.showGraphs = false;
    }
    this.activeTabName = event.tab.textLabel;
  }

  /**
   * SOCKET - Listen for CV score calculation completed messages
   */
  initCvSocket(): void {
    this._socket.listen('cvCompleted').subscribe(
      async (respData: any) => {
        if (respData.modelData.status === 'failed') {
          this.showSpinner = false;
          this.trainings[0].models = this.trainings[0].models?.map((data: any) => {
            if (data._id === respData.modelData?.modelId) {
              data.modelMetaData.cvData.status = 'completed';
            }
            return data;
          });
          this.updateTrainingModels.emit(this.trainings[0].models);
          this._toaster.error(respData.modelData.message, 'Models');
        } else {
          this.trainings[0].models = this.trainings[0].models?.map((data: any) => {
            if (data._id === respData.modelData?._id) {
              data = respData.modelData;
            }
            return data;
          });
          this.updateTrainingModels.emit(this.trainings[0].models);
          this._toaster.success('CV score calculated successfully', 'Models');
          this.showSpinner = false;
        }
      },
      (err: Error) => {
        this._toaster.error(`Socket Error - ${err.message}`, 'Train Model');
        this.showSpinner = false;
      }
    );
  }
  /**
   * Create an empty array for k-fold
   * @param data
   * @returns
   */
  getArray(data: any): Array<any> {
    return new Array(data);
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this._socket.removeListener('cvCompleted');
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
