import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { LocalStorageService } from 'src/app/shared/local-storage.service';
import { SpinnerService } from 'src/app/shared/spinner.service';
import { ModelsService } from '../models.service';

@Component({
  selector: 'app-pipeline',
  templateUrl: './pipeline.component.html',
  styleUrls: ['./pipeline.component.scss'],
})
export class PipelineComponent implements OnInit {
  unsubscribe$ = new Subject<boolean>();
  pipelineTask: any = [];
  timelineAlt: any = true;
  selectedTask: any;
  showPipelineDetails = {};
  project: any;
  taskData: any = {};
  columnList: any = {};
  method: any = {};
  shape: any = {};
  step: any;
  pipelineDetails: any;
  afOperation: any;

  constructor(
    private _localStorage: LocalStorageService,
    @Inject(MAT_DIALOG_DATA) public dialogData: any,
    private _toaster: ToastrService,
    private _spinner: SpinnerService,
    private _models: ModelsService
  ) { }

  ngOnInit(): void {
    this.project = this._localStorage.getItem('project');
    this.pipelineTask = this.dialogData.pipelineTask;
    this.pipelineTask.forEach(element => {
      let taskName = element.parent == 'Training' ? element.data : 'none';
      if (taskName !== 'none') {
        taskName.forEach(item => {
          if (item.taskName == 'Auto Feature Generation') {
            this.afOperation = item.afOperations[0];
          }
        });
      }
    });
  }

  /**
   * Fetches selected pipeline task details when clicked on view details
   * @param index
   * @param transformer
   * @param j
   */
  getPipelineTaskDetails(i: any, transformer: any, j: any): void {
    this.step = `${i}${j}`;
    this.selectedTask = transformer;
    let reqObj: any;
    reqObj = {
      transformer: transformer,
      trainingId: this.dialogData.trainings[0]._id,
      projectType: this.project.type,
      indepVariable: this.dialogData.trainings[0].indepVariable,
      filename: this.dialogData.trainings[0].metaInfo.fileName,
      indexPath: this.dialogData.trainings[0].edaId.indexPath,
    };
    if (this.dialogData.originalFileWithEdaDetails.taExperimentId) {
      reqObj.taExperimentId = this.dialogData.originalFileWithEdaDetails.taExperimentId;
    }
    if (transformer == 'sampling') {
      (reqObj.samplingType = this.dialogData.trainings[0].sampling),
        (reqObj.samplingPercentage = this.dialogData.trainings[0].samplingPercentage);
    }
    if (transformer == 'auto_feature_generate') {
      reqObj.operation = this.afOperation;
    }
    if (this.project.type == 'predictive_modeling') {
      reqObj.predictiveModelingInfo = this.dialogData.trainings[0].predictiveModelingInfo;
    } else if (this.project.type == 'clustering') {
      reqObj.clusteringInfo = this.dialogData.trainings[0].clusteringInfo;
    }
    // API call
    this._models
      .getPipelineDetails(this.dialogData.trainings[0]._id, reqObj)
      .pipe(takeUntil(this.unsubscribe$))
      .subscribe(
        (resp) => {
          if (resp) {
            this.pipelineDetails = resp;
          }
          this._spinner.stopSpinner();
        },
        (err: any) => {
          this._toaster.error(`${err.error.message || 'Server Error'}`, 'Models');
          this._spinner.stopSpinner();
        }
      );
  }

  /**
   * Perform operations when the module is destroyed
   */
  ngOnDestroy() {
    this.unsubscribe$.next(false);
    this.unsubscribe$.unsubscribe();
  }
}
